@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  font-family: 'Righteous', cursive;
}
.fadeIn{
  -webkit-animation: In 1.5s linear;
          animation: In 1.5s linear;
}
@-webkit-keyframes In {
  0%{
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}
@keyframes In {
  0%{
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}
.bgMenu{
  background-color: #000;
  transition: .7s ease-in-out;
}
.hidenMenu{
  top:-110px;
}
.showMenu{
  top:0px;
}

.navbar-dark .navbar-nav .nav-link {
  color: aliceblue;
}

.banner{
  height:100vh;
  background-image:url(/webtechnologysoftware/static/media/banner2.6952de7e.jpg);
  background-position:-150px;
  background-color:#000;
  background-repeat:no-repeat;
  background-size:175%;
  background-attachment: fixed;
  color: aliceblue;
}
.center{
  height:75vh;
  display: flex;
  
  align-items: center;
}
.message{
  margin-top: 100px;
}

.message p{
  font-weight: 100;
}

.parallax{
  min-height: 420px;
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  background-position: center;
  background-repeat: no-repeat;
 
  background-attachment: fixed;
  color: aliceblue;
}
.parallaxBgOne{
  background-image: url(/webtechnologysoftware/static/media/parallax.2dca4d1c.png);
  background-size: cover;
}
.parallaxBgTwo{
  background-image: url(/webtechnologysoftware/static/media/parallax2.c4a2d66f.jpg);
  background-size: contain;
  background-color: #000;
}

.gray{
 
  background-color: #F7F7F7;
  padding-top: 5%;
}
.white{
  background-color: white;
}
footer{

  background-color: black;
}
.bgFooter{
  border-top: 7px solid #FFE68D;
  height:580px;
  background-image: linear-gradient(90deg, rgba(0, 0, 0, 0.5), 
  rgba(0, 0, 0, 0.5)),url(/webtechnologysoftware/static/media/bgFooter.7d94f8de.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: white;
}
button.btn-second{
  border: 3px solid white;
  border-radius: 25px;
  padding: 7px 15px;
  color: white;
  background-color: transparent;

}
.footerIn{
  position: relative;
  width: 100%;
  height: 140px;
  display: flex;
  align-items: flex-end;
  justify-content: center;
}
.copyright{
